export default class ConferenceProceedings extends BaseHandler {
    constructor() {
        super();
        this.requiredFields = [
            'title',
            'conferencePublicationName',
            'city',
            'year'
        ];
        this.otherFields = [
            'publisher',
            'volume',
            'pages',
            'editor',
            'shortTitle',
            'standardNumber'
        ];
    }

    /**
     * Sets publisher.
     *
     * @param {string} data
     * @returns {void}
     */
    setConferencePublicationName = (data = null) => {
        this.allData.conferencePublicationName = data;
    };

    /**
     * Sets shortTitle.
     *
     * @param {string} data
     * @returns {void}
     */
    setShortTitle = (data = null) => {
        this.allData.shortTitle = data;
    };

    /**
     * Sets standardNumber.
     *
     * @param {string} data
     * @returns {void}
     */
    setStandardNumber = (data = null) => {
        this.allData.standardNumber = data;
    };
}
